import React from 'react';
import { useHistory } from 'react-router-dom';
import { Row } from 'reactstrap';
import { Colxx } from '../../../components/common/CustomBootstrap';
import ConnectorsList from '../../../components/custom/connectors/ConnectorsList';

const List = () => {
  const history = useHistory();
  const handleClick = () => {
    history.push('/app/connectors/new-source');
  };
  return (
    <>
      <Row>
        <Colxx xxs="12">
          <div className="experimentsMenu">
            <div className="experimentsMenuLeft">
              <h2>Connectors</h2>
            </div>
            <div className="experimentsMenuRight">
              <button
                type="button"
                className="experimentsMenuButton"
                onClick={() => {
                  handleClick();
                }}
              >
                <i className="iconsminds-add" />
                New connection
              </button>
            </div>
          </div>
          <hr />
        </Colxx>
      </Row>
      <Row>
        <Colxx xxs="12">
          <ConnectorsList />
        </Colxx>
      </Row>
    </>
  );
};
export default List;
